// List.countdown.tsx
//--|ðŸ ‹ Styles ðŸ ‹|--//
import './List.countdown.scss';
//--|ðŸ ‰ Styles ðŸ ‰|--//
//--|ðŸ ‹ Dependencies ðŸ ‹|--//
import axios, { AxiosError } from 'axios';
import React, { useEffect, useState, createContext, useContext } from 'react';
//--|ðŸ ‰ Dependencies ðŸ ‰|--//
//--|ðŸ ‹ Context ðŸ ‹|--//
//--|ðŸ ‰ Context ðŸ ‰|--//
//--|ðŸ ‹ Components ðŸ ‹|--//
//--|ðŸ ‰ Components ðŸ ‰|--//
//--|ðŸ ‹ Functions ðŸ ‹|--//
import { stripBrackets } from '../../../scripts/landing';
import { scaleText, createSquare } from './List_countdown';
import { viewBlock, viewText, axiosError } from '../../../scripts/landing';
//--|ðŸ ‰ Functions ðŸ ‰|--//
interface InfoProps {
  info: {
    pageName: '[landing]' | '[overtime]' | '[ticketing]' | '[hyperlink]' | string;
    blockName: '<overlay>' | '<leftbar>' | '<rightbar>' | '<header>' | '<footer>' | '<main>' | string;
    roleName?: '(established)' | '(freelancing)' | '(manager)' | '(employee)' | '(specialist)' | '(technician)' | string;
  };
}

type Counters = {
  //--|ðŸ ‹ Define a type alias for the countdown state ðŸ ‹|--//
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
};
const ListCountdown: React.FC<InfoProps> = ({ info }) => {
  const pageName = stripBrackets(info.pageName, '[]') as 'landing';
  const blockName = stripBrackets(info.blockName, '<>') as 'footer';

  //--|ðŸ ‹ Local Input States ðŸ ‹|--//
  let [time, setTime] = useState({ days: 0, hours: 0, minutes: 0, seconds: 0 });

  useEffect(() => {
    let refreshClock = scaleText();
    let refreshSquare = createSquare(); // Runs `createSquare()` and gets cleanup function
    let refreshTimer = loadTimer(setTime); // Runs `loadTimer()`

    return () => {
      refreshClock();
      refreshSquare(); // Cleans up the resize event listener
      refreshTimer?.(); // If `loadTimer` returns a cleanup function, call it
    };
  }, [pageName, blockName]);

  return (
    <ol className="list-countdown">
      <li className="clock-days">
        <span>{time.days}</span>
      </li>
      <li className="clock-hours">
        <span>{String(time.hours).padStart(2, '0')}</span>
      </li>
      <li className="clock-minutes">
        <span>{String(time.minutes).padStart(2, '0')}</span>
      </li>
      <li className="clock-seconds">
        <span>{String(time.seconds).padStart(2, '0')}</span>
      </li>
    </ol>
  );
};
export default ListCountdown;

const loadTimer = (setTime: React.Dispatch<React.SetStateAction<Counters>>) => {
  //--|ðŸ ‹ Select the footer element with an ID containing "footer" ðŸ ‹|--//
  let footer = document.querySelector('footer[id*="footer"]');
  let interval: NodeJS.Timeout | null = null; //--|ðŸ ˆ Store reference to setInterval, so we can clear it later ðŸ ˆ|--//

  let updateCountdown = (targetDate: Date) => {
    /**
     * Function to calculate the remaining time and update state
     * @param targetDate - The future date when restriction expires
     **/
    var present = new Date(); //--|ðŸ ˆ Get the current time ðŸ ˆ|--//
    var interval = targetDate.getTime() - present.getTime(); //--|ðŸ ˆ Difference between target and current time in milliseconds ðŸ ˆ|--//

    if (interval <= 0) {
      //--|ðŸ ‹ If the countdown has expired, set everything to 0 ðŸ ‹|--//
      setTime({ days: 0, hours: 0, minutes: 0, seconds: 0 });
      return;
    }

    //--|ðŸ ‹ Calculate time left in days, hours, minutes, and seconds ðŸ ‹|--//
    setTime({
      days: Math.floor(interval / (1000 * 60 * 60 * 24)), //--|ðŸ ˆ Convert milliseconds to days ðŸ ˆ|--//
      hours: Math.floor((interval / (1000 * 60 * 60)) % 24), //--|ðŸ ˆ Convert to hours, keeping remainder within 24h ðŸ ˆ|--//
      minutes: Math.floor((interval / (1000 * 60)) % 60), //--|ðŸ ˆ Convert to minutes, keeping remainder within 60min ðŸ ˆ|--//
      seconds: Math.floor((interval / 1000) % 60), //--|ðŸ ˆ Convert to seconds, keeping remainder within 60sec ðŸ ˆ|--//
    });
  };

  let observeFooter = new MutationObserver((mutationsList) => {
    /**
     * MutationObserver listens for attribute changes on the footer (e.g., class changes)
     **/
    for (const mutation of mutationsList) {
      if (mutation.attributeName === 'class') {
        const currentClass = (mutation.target as HTMLElement).className; //--|ðŸ ˆ Get the new class name ðŸ ˆ|--//

        if (currentClass.includes('expanded')) {
          //--|ðŸ ‰ Footer has been expanded, so fetch the countdown from the server ðŸ ‰|--//

          //--|ðŸ ‹ Select the email input field inside a div that contains "inputs" in its class ðŸ ‹|--//
          const emailInput = document.querySelector<HTMLInputElement>('div[class*="inputs"] #email');

          if (!emailInput) {
            console.error('Email input not found.');
            return; //--|ðŸ ˆ Stop execution if the email input field is missing ðŸ ˆ|--//
          }

          //--|ðŸ ‹ Make an API request to fetch the countdown expiration date ðŸ ‹|--//
          let route = 'blocked';
          let link = 'http://localhost:3000';
          axios
            .post(`${link}/users/${route}`, { email: emailInput.value })
            .then((response) => {
              const { data } = response.data; //--|ðŸ ˆ Extract the relevant data ðŸ ˆ|--//
              const targetDate = new Date(data.restrictionExpiresAt); //--|ðŸ ˆ Convert to a Date object ðŸ ˆ|--//

              //--|ðŸ ‹ Immediately update countdown when data is received ðŸ ‹|--//
              updateCountdown(targetDate);

              //--|ðŸ ‹ Clear any existing interval before setting a new one ðŸ ‹|--//
              if (interval) clearInterval(interval);

              //--|ðŸ ‹ Start a new interval to update countdown every second ðŸ ‹|--//
              interval = setInterval(() => updateCountdown(targetDate), 1000);
            })
            .catch((error) => console.error('Error fetching countdown:', error));
        }
      }
    }
  });
  if (!footer) return; //--|ðŸ ˆ Exit early if the footer is not found ðŸ ˆ|--//
  observeFooter.observe(footer, { attributes: true }); //--|ðŸ ˆ Start observing the footer for attribute changes (such as class changes) ðŸ ˆ|--//

  /**
   * Cleanup function - Runs when component unmounts or dependencies change
   **/
  return () => {
    observeFooter.disconnect(); //--|ðŸ ˆ Stop observing the footer ðŸ ˆ|--//
    if (interval) clearInterval(interval); //--|ðŸ ˆ Clear the countdown interval if it exists ðŸ ˆ|--//
  };
};
